import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { workshopAPI, API_ROOT } from '../services/api';
import SEOHead from '../components/SEOHead';
import './Workshops.css';

const Workshops = () => {
    const [workshops, setWorkshops] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');

    useEffect(() => {
        const fetchWorkshops = async () => {
            try {
                const response = await workshopAPI.getAll();
                // Filter only active workshops
                const activeWorkshops = response.data.filter(w => w.is_active);
                setWorkshops(activeWorkshops);
            } catch (err) {
                setError('Error al cargar los talleres');
                console.error('Error fetching workshops:', err);
            } finally {
                setLoading(false);
            }
        };
        fetchWorkshops();
    }, []);

    const formatDate = (dateString) => {
        if (!dateString) return '';
        const date = new Date(dateString);
        return date.toLocaleDateString('es-ES', {
            day: 'numeric',
            month: 'long',
            year: 'numeric'
        });
    };

    const getImageUrl = (workshop) => {
        if (workshop.images && workshop.images.length > 0 && workshop.images[0].image_url) {
            const imageUrl = workshop.images[0].image_url;
            if (imageUrl.startsWith('http')) {
                return imageUrl;
            }
            return `${API_ROOT}/uploads/talleres/${imageUrl}`;
        }
        return null;
    };

    const getTotalAttendees = (workshop) => {
        const online = parseInt(workshop.registration_count) || 0;
        const manual = parseInt(workshop.manual_attendees) || 0;
        return online + manual;
    };

    if (loading) {
        return (
            <div className="workshops-page">
                <div className="container">
                    <div className="loading">Cargando talleres...</div>
                </div>
            </div>
        );
    }

    return (
        <div className="workshops-page">
            <SEOHead
                title="Talleres y Formaciones | Esencialmente Psicologa"
                description="Descubre nuestros talleres de psicologa en Barcelona. Mindfulness, gestin del estrs, inteligencia emocional y ms. Grupos reducidos con profesionales especializados."
                keywords="talleres psicologa Barcelona, mindfulness, gestin estrs, inteligencia emocional, formacin psicolgica"
            />

            {/* Hero Section */}
            <section className="workshops-hero">
                <div className="container">
                    <h1>Talleres y Formaciones</h1>
                    <p>Aprende y crece con nuestros talleres especializados</p>
                </div>
            </section>

            <div className="workshops-content">
                <div className="container">
                    {error && (
                        <div className="error-message">{error}</div>
                    )}

                    {workshops.length === 0 && !error ? (
                        <div className="no-workshops">
                            <div className="no-workshops-icon"></div>
                            <h2>Prximamente</h2>
                            <p>Estamos preparando nuevos talleres. Vuelve pronto para descubrir nuestras prximas formaciones!</p>
                            <Link to="/contacto" className="btn btn-primary">
                                Suscrbete para recibir novedades
                            </Link>
                        </div>
                    ) : (
                        <div className="workshops-grid">
                            {workshops.map((workshop) => {
                                const isClickable = workshop.is_clickable !== false;
                                const CardWrapper = isClickable ? Link : 'div';
                                const cardProps = isClickable
                                    ? { to: `/talleres/${workshop.slug || workshop.id}` }
                                    : {};

                                return (
                                    <article
                                        key={workshop.id}
                                        className={`workshop-card ${!isClickable ? 'not-clickable' : ''}`}
                                    >
                                        <div className="workshop-image">
                                            {getImageUrl(workshop) ? (
                                                <img
                                                    src={getImageUrl(workshop)}
                                                    alt={workshop.title}
                                                    onError={(e) => {
                                                        e.currentTarget.style.display = 'none';
                                                        e.currentTarget.nextSibling.style.display = 'flex';
                                                    }}
                                                />
                                            ) : null}
                                            <div className="placeholder-image" style={{ display: getImageUrl(workshop) ? 'none' : 'flex' }}>
                                                <span></span>
                                            </div>
                                            <div className="workshop-date-badge">
                                                <span className="day">{new Date(workshop.start_date).getDate()}</span>
                                                <span className="month">{new Date(workshop.start_date).toLocaleDateString('es-ES', { month: 'short' })}</span>
                                            </div>
                                        </div>

                                        <div className="workshop-body">
                                            <h2 className="workshop-title">{workshop.title}</h2>

                                            <p className="workshop-description">
                                                {workshop.description.length > 120
                                                    ? workshop.description.substring(0, 120) + '...'
                                                    : workshop.description}
                                            </p>

                                            <div className="workshop-meta">
                                                <div className="meta-item">
                                                    <span className="meta-icon"></span>
                                                    <span className="meta-value">{workshop.price}</span>
                                                </div>
                                                {workshop.location && (
                                                    <div className="meta-item">
                                                        <span className="meta-icon"></span>
                                                        <span className="meta-value">{workshop.location}</span>
                                                    </div>
                                                )}
                                                {workshop.show_attendees_count && workshop.max_participants && (
                                                    <div className="meta-item">
                                                        <span className="meta-icon"></span>
                                                        <span className="meta-value">
                                                            {getTotalAttendees(workshop)}/{workshop.max_participants} plazas
                                                        </span>
                                                    </div>
                                                )}
                                                {workshop.show_attendees_count && !workshop.max_participants && (
                                                    <div className="meta-item">
                                                        <span className="meta-icon"></span>
                                                        <span className="meta-value">
                                                            {getTotalAttendees(workshop)} inscritos
                                                        </span>
                                                    </div>
                                                )}
                                                {!workshop.show_attendees_count && workshop.max_participants && (
                                                    <div className="meta-item">
                                                        <span className="meta-icon"></span>
                                                        <span className="meta-value">Mx. {workshop.max_participants} personas</span>
                                                    </div>
                                                )}
                                            </div>

                                            {isClickable ? (
                                                <Link
                                                    to={`/talleres/${workshop.slug || workshop.id}`}
                                                    className="btn btn-primary workshop-btn"
                                                >
                                                    Ver Detalles
                                                </Link>
                                            ) : (
                                                <span className="btn btn-disabled workshop-btn">
                                                    Prximamente
                                                </span>
                                            )}
                                        </div>
                                    </article>
                                );
                            })}
                        </div>
                    )}

                    <div className="cta-section">
                        <h2>Tienes alguna pregunta?</h2>
                        <p>Contacta con nosotros para ms informacin sobre nuestros talleres</p>
                        <Link to="/contacto" className="btn btn-primary">Contactar</Link>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Workshops;
