import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { therapistAPI, pricingAPI, workshopAPI } from '../services/api';
import TherapistsTab from '../components/TherapistsTab';
import PricingTab from '../components/PricingTab';
import WorkshopsTab from '../components/WorkshopsTab';
import BlogTab from '../components/BlogTab';
import BillingTab from '../components/BillingTab';
import BillingDashboard from '../components/BillingDashboard';
import PatientsTab from '../components/PatientsTab';
import RemindersTab from '../components/RemindersTab';
import SyncTab from '../components/SyncTab';
import './AdminDashboard.css';

const AdminDashboard = () => {
  const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';
  const navigate = useNavigate();
  const [user, setUser] = useState(null);
  const [activeTab, setActiveTab] = useState('hub'); // Start at 'hub'

  // Password Change State
  const [showPasswordModal, setShowPasswordModal] = useState(false);
  const [pwdCurrent, setPwdCurrent] = useState('');
  const [pwdNew, setPwdNew] = useState('');
  const [pwdMsg, setPwdMsg] = useState({ type: '', text: '' });
  const [therapists, setTherapists] = useState([]);
  const [pricing, setPricing] = useState([]);
  const [workshops, setWorkshops] = useState([]);
  const [loading, setLoading] = useState(true);

  // Calendar ID provided by user
  const CALENDAR_ID = 'esencialmentepsicologia@gmail.com';

  const checkAuth = useCallback(() => {
    const token = localStorage.getItem('token');
    const userData = localStorage.getItem('user');

    if (!token || !userData) {
      navigate('/admin');
      return;
    }

    try {
      const parsedUser = JSON.parse(userData);
      // Ensure role exists (handle legacy login data)
      if (!parsedUser.role) parsedUser.role = 'admin';
      setUser(parsedUser);
    } catch (error) {
      navigate('/admin');
    }
  }, [navigate]);

  const fetchData = useCallback(async () => {
    try {
      setLoading(true);
      // Solo fetch si es admin, o si se requiere para una vista.
      // Si el user es terapeuta, quizs no necesita cargar todos los workhops etc.
      // Pero por simplicidad cargamos.
      const [therapistsRes, pricingRes, workshopsRes] = await Promise.all([
        therapistAPI.getAll(),
        pricingAPI.getAll(),
        workshopAPI.getAll(true)
      ]);
      setTherapists(therapistsRes.data);
      setPricing(pricingRes.data);
      setWorkshops(workshopsRes.data);
    } catch (error) {
      console.error('Error fetching data:', error);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    checkAuth();
  }, [checkAuth]);

  useEffect(() => {
    // Fetch data only if user is logged in
    if (user && user.role === 'admin') {
      fetchData();
    }
  }, [user, fetchData]);

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    navigate('/admin');
  };

  const handleChangePassword = async (e) => {
    e.preventDefault();
    setPwdMsg({ type: '', text: '' });

    try {
      const token = localStorage.getItem('token');
      const res = await fetch(`${API_URL}/auth/change-password`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`
        },
        body: JSON.stringify({ currentPassword: pwdCurrent, newPassword: pwdNew })
      });
      const data = await res.json();

      if (res.ok) {
        setPwdMsg({ type: 'success', text: 'Contrasea actualizada correctamente' });
        setPwdCurrent('');
        setPwdNew('');
        setTimeout(() => setShowPasswordModal(false), 2000);
      } else {
        setPwdMsg({ type: 'error', text: data.message || 'Error al cambiar la contrasea' });
      }
    } catch (error) {
      setPwdMsg({ type: 'error', text: 'Error de conexin' });
    }
  };

  const renderPasswordModal = () => {
    if (!showPasswordModal) return null;
    return (
      <div className="modal-overlay">
        <div className="modal-content password-modal">
          <h3> Cambiar Contrasea</h3>
          <form onSubmit={handleChangePassword}>
            <div className="form-group">
              <label>Contrasea Actual:</label>
              <input
                type="password"
                value={pwdCurrent}
                onChange={(e) => setPwdCurrent(e.target.value)}
                required
              />
            </div>
            <div className="form-group">
              <label>Nueva Contrasea:</label>
              <input
                type="password"
                value={pwdNew}
                onChange={(e) => setPwdNew(e.target.value)}
                required
              />
            </div>
            {pwdMsg.text && <p className={`msg ${pwdMsg.type}`}>{pwdMsg.text}</p>}
            <div className="modal-actions">
              <button type="submit" className="btn-confirm">Actualizar</button>
              <button type="button" className="btn-cancel" onClick={() => setShowPasswordModal(false)}>Cancelar</button>
            </div>
          </form>
        </div>
      </div>
    );
  };

  const HubCard = ({ title, icon, color, onClick, description }) => (
    <div className="hub-card" onClick={onClick} style={{ borderTopColor: color }}>
      <div className="hub-icon" style={{ backgroundColor: color + '20', color: color }}>
        {icon}
      </div>
      <h3>{title}</h3>
      <p>{description}</p>
    </div>
  );

  const renderHub = () => (
    <div className="admin-hub-grid">
      {/* Billing is available for Everyone (view tailored by role) */}
      <HubCard
        title="Facturacin y Horas"
        icon=""
        color="#4285F4"
        description={user.role === 'admin' ? "Gestin financiera del equipo" : "Mis horas semanales"}
        onClick={() => setActiveTab('billing-hub')}
      />

      {/* Admin Only Sections */}
      {user.role === 'admin' && (
        <>
          <HubCard
            title="Pacientes"
            icon=""
            color="#00BCD4"
            description="Ver y gestionar pacientes"
            onClick={() => setActiveTab('patients')}
          />
          <HubCard
            title="Terapeutas"
            icon=""
            color="#0F9D58"
            description="Gestionar perfiles del equipo"
            onClick={() => setActiveTab('therapists')}
          />
          <HubCard
            title="Precios y Tarifas"
            icon=""
            color="#F4B400"
            description="Editar precios de sesiones"
            onClick={() => setActiveTab('pricing')}
          />
          <HubCard
            title="Talleres"
            icon=""
            color="#DB4437"
            description="Crear y gestionar talleres"
            onClick={() => setActiveTab('workshops')}
          />
          <HubCard
            title="Blog"
            icon=""
            color="#9C27B0"
            description="Escribir artculos y noticias"
            onClick={() => setActiveTab('blog')}
          />
          <HubCard
            title="Recordatorios"
            icon=""
            color="#E91E63"
            description="Cola de emails recordatorios"
            onClick={() => setActiveTab('reminders')}
          />
        </>
      )}
    </div>
  );

  // Billing Submenu Hub - Simplificado
  const renderBillingHub = () => (
    <div className="admin-hub-grid">
      <HubCard
        title="Sesiones Semanales"
        icon=""
        color="#4285F4"
        description="Ver y gestionar pagos por semana"
        onClick={() => setActiveTab('billing')}
      />
      {user.role === 'admin' && (
        <HubCard
          title="Reportes y Estadsticas"
          icon=""
          color="#AA00FF"
          description="Dashboard con totales y tendencias"
          onClick={() => setActiveTab('billing-dashboard')}
        />
      )}
    </div>
  );

  if (!user) return null;

  return (
    <div className="admin-dashboard">
      <header className="dashboard-header">
        <div className="header-content">
          <div className="logo-section">
            <h1>{activeTab === 'hub' ? 'Panel de Gestin' : getTitle(activeTab)}</h1>
          </div>

          <div className="header-actions">
            <div className="user-info">
              <span className="user-role">{user.role === 'admin' ? 'Administrador' : 'Terapeuta'}</span>
              <span className="user-email">{user.email}</span>
            </div>
            <button onClick={() => setShowPasswordModal(true)} className="btn btn-secondary btn-sm">
               Contrasea
            </button>
            <button onClick={handleLogout} className="btn btn-secondary btn-sm">
              Salir
            </button>
          </div>
        </div>
      </header>

      <div className="dashboard-content-wrapper">
        {/* Back Button - Now in content area */}
        {activeTab !== 'hub' && (
          <div className="content-back-nav">
            <button className="btn-back-content" onClick={() => setActiveTab(activeTab === 'billing' ? 'billing-hub' : 'hub')}>
               {activeTab === 'billing' ? 'Volver a Facturacin' : 'Volver al Men'}
            </button>
          </div>
        )}

        {activeTab === 'hub' && renderHub()}
        {activeTab === 'billing-hub' && renderBillingHub()}

        {/* Content Views */}
        <div className={`tab-content ${!['hub', 'billing-hub'].includes(activeTab) ? 'visible' : 'hidden'}`}>
          {activeTab === 'therapists' && <TherapistsTab therapists={therapists} onRefresh={fetchData} />}
          {activeTab === 'pricing' && <PricingTab pricing={pricing} onRefresh={fetchData} />}
          {activeTab === 'workshops' && <WorkshopsTab workshops={workshops} onRefresh={fetchData} />}
          {activeTab === 'blog' && <BlogTab />}
          {activeTab === 'billing' && <BillingTab user={user} calendarId={CALENDAR_ID} />}
          {activeTab === 'billing-dashboard' && <BillingDashboard user={user} />}
          {activeTab === 'patients' && <PatientsTab user={user} />}
          {activeTab === 'reminders' && <RemindersTab />}
        </div>
      </div>
      {renderPasswordModal()}
    </div>
  );
};

// Helper
const getTitle = (tab) => {
  switch (tab) {
    case 'therapists': return 'Gestin de Terapeutas';
    case 'pricing': return 'Precios y Tarifas';
    case 'workshops': return 'Gestin de Talleres';
    case 'blog': return 'Blog y Noticias';
    case 'billing-hub': return 'Facturacin y Horas';
    case 'billing': return 'Sesiones Semanales';
    case 'billing-dashboard': return 'Reportes y Estadsticas';
    case 'patients': return 'Gestin de Pacientes';
    case 'reminders': return 'Cola de Recordatorios';
    default: return 'Panel';
  }
};

export default AdminDashboard;
